.gnb {
    // gnb
	&._gd-2 {
		$grid: 'gnb_gd-2_';

		@include mixin-respond(mobile_tablet) {
			grid-template-columns: 77px 1fr auto;
			grid-template-rows: 79px;
			grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3";
		}
		
		@include mixin-respond(desktop) {
			grid-template-columns: 1fr;
			grid-template-rows: auto 1fr auto;
			grid-template-areas: "#{$grid}1" "#{$grid}2" "#{$grid}3";
		}
		
		@include mixin-gd_item($grid, 3);
	}
	
	&._area {
		&-1 {
			$val: 20px;

			padding-left: $val;
			padding-right: $val;
		}
		&-2 {
			$val: 0.4em;
			margin-top: $val;
			margin-bottom: $val;
		}
  }
    
    // mobile

}