.pg {
	
	&._title {
		&-1 {
			margin-bottom: 18px;
			font-size: 44px;
		}
		&-2 {
			font-size: 21px;
		}
	}

	&._area {
		&-1 {
			// margin-top: 49px;
			margin-bottom: 48px;
		}
		&-2 {
			margin-top: 97px;
			margin-bottom: 68px;
		}
		&-3 {
			max-width: 560px;
			// width: 100%;
		}
		&-4 {
			margin-top: 18px;
			margin-bottom:4px;
		}
		&-5 {
			$val1: 20px;
			$val2: 10px;
			
			width: 100%;
			
			@include mixin-respond(mobile) {
				padding-left: $val2;
				padding-right: $val2;
				padding-top: 20px;
				padding-bottom: 20px;
			}
			
			@include mixin-respond(tablet) {
				padding-left: $val1;
				padding-right: $val1;
				padding-top: 40px;
				padding-bottom: 40px;
			}
			@include mixin-respond(desktop) {
				max-width: 1200px;
				width: 100%;
				padding-left: $val1;
				padding-right: $val1;
				padding-top: 114px;
				padding-bottom: 40px;
			}
		}
	}
	
	&._gd {
		&-1 {
			$grid: 'lc_gd-1_';
	
			@include mixin-respond(mobile) {
				grid-row-gap: 30px;
				grid-column-gap: 10px;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: auto auto auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2" "#{$grid}3 #{$grid}4" "#{$grid}5 #{$grid}6" "#{$grid}7 #{$grid}8" "#{$grid}9 #{$grid}10";
			}
			@include mixin-respond(tablet) {
				grid-row-gap: 40px;
				grid-column-gap: 30px;
				grid-template-columns: 1fr 1fr 1fr;
				grid-template-rows: auto auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3" "#{$grid}4 #{$grid}5 #{$grid}6" "#{$grid}7 #{$grid}8 #{$grid}9" "#{$grid}10 . .";
			}
			@include mixin-respond(desktop) {
				grid-row-gap: 40px;
				grid-column-gap: 20px;
				grid-template-columns: 1fr 1fr 1fr 1fr;
				grid-template-rows: auto auto auto;
				grid-template-areas: "#{$grid}1 #{$grid}2 #{$grid}3 #{$grid}4"
					"#{$grid}5 #{$grid}6 #{$grid}7 #{$grid}8" 
					"#{$grid}9 #{$grid}10 . .";
			}
	
			@include mixin-gd_item($grid, 10); 
		}
  }
}